<template>
  <div class="m-index">
    <div class="index-hd">
      <img src="./img/h-title.png" alt="你的行测弱点在哪一块？" class="h-title">
      <img src="./img/h-icon.png" alt="测一测" class="h-icon">
    </div>
    <div class="index-bd">
      <h4 class="b-title"><img src="./img/b-title.png" alt="测评说明"></h4>
      <div class="desc">该测评为行测5大模块的精简版测试，满分为100分。本次测试共有25题，均为单选题，请您认真作答，答题完毕后可查看您的行测弱点究竟在哪里，并查看错题的解析</div>
      <img src="./img/b-book.png" alt="book" class="book">
      <img src="./img/b-pen.png" alt="pen" class="pen">
    </div>
    <div class="index-fd">
      <div class="msg">参与测评人数：{{joinNumber}}</div>
      <div class="submit-btn" @click='startTo'>{{statusText}}</div>
    </div>
  </div>
</template>

<script>
import { Subject } from '@/assets/api/subject'
import { getStore,setStore } from '@/assets/js/utils'
import { config } from '@/assets/js/config'

export default {
  name: 'home',
  data () {
    return {
      uid:getStore(config.USERID),
      joinNumber:0,             // 参与人数
      statusText:'开始测评',    // 继续测评  查看评测结果
    }
  },
  created(){
    this.$nextTick(()=>{
      this._initStatus()
    })
  },
  methods:{
    // 获取测评状态
    _initStatus(){
      Subject.getStatus({
        userId:this.uid
      }).then(res=>{
        if(res.code == 1){
          this.joinNumber = res.data.joinInNum
          this.status = res.data.isJionIn
          if(this.status == 1){
            this.statusText = '查看评测结果'
            setStore(config.STATUS, 1)
          }else if(getStore(config.STATUS) == 2){
            this.statusText = '继续测评'            
          }else{
            setStore(config.STATUS, 0)
          }
        }
      })
    },
    startTo(){
      if(this.status == 1){
        this.$router.push({name:'result'})
      }else{
        this.$router.push({name:'subject'})
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/style/variable.scss";
.m-index{
  min-height: 100vh;
  padding:120px 30px 80px;
  box-sizing: border-box;
  background:url('./img/bg.png') no-repeat #fe4f4f;
  background-size:contain;
}
.index-hd{
  position: relative;
  text-align: center;
  .h-title{
    width:604px;
  }
  .h-icon{
    width:135px;
    position:absolute;
    top:-60px;
    left:320px;
  }
}
.index-bd{
  margin-top:60px;
  width:690px;
  padding:50px 30px;
  box-sizing: border-box;
  margin-left:auto;
  margin-right:auto;
  background-color:rgba(255,255,255,.95);
  border-radius:10px;
  position: relative;
  .b-title{
    width:363px;
    margin: 40px auto;    
    img{
      width:100%;
      display: block;
    }
  }
  .desc{
    line-height: 1.8;
  }
  .book{
    width:103px;
    position: absolute;
    right:-30px;
    top:-90px;
  }
  .pen{
    width:55px;
    position: absolute;
    left:-5px;
    top:-70px;
  }
}
.index-fd{
  width:650px;
  margin:60px auto 0;
  .msg{
    color:$cl-white;
    margin-bottom:15px;
    text-align: center;
  }
  .submit-btn{
    height:112px;
    color:$cl-black;
    font-size:42px;
    text-align: center;
    line-height: 112px;
    border-radius:112px;
    background:linear-gradient(#fff140,#ffba35);
  }
}
</style>



